
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { COMMON_KEY_VALUE } from '@/shared/constants/common.const';
import { RPT203000111_REQ_BODY, CustomerInformationReportDataType, RPT203000111_RES_BODY } from '@/shared/type/RPT203000111.type'
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
import { COM21200113_RES, userRole } from "@/shared/type/COM21200113.type";

const excel = new ExportExcel()

export default defineComponent({
    components: { Pagination, VueDatePicker },
    data() {
        return {
            customerInformationReportData: [] as CustomerInformationReportDataType[],
            COMMON_KEY_VALUE,
            header: [
                { columnName: 'No' },
                { columnName: 'Branch Code' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'Username' },
                { columnName: 'User Role' },
                { columnName: 'Account Assignation' },
                { columnName: 'Phone Number' },
                { columnName: 'Email ID' },
                { columnName: 'Date Of Birth' },
                { columnName: 'Address' },
                { columnName: 'User Type' },
                { columnName: 'Authorization Status' },
                { columnName: 'Create By' },
                { columnName: 'Create Date' },
                { columnName: 'Authorization Create By' },
                { columnName: 'Authorization Create Date' },
                { columnName: 'Update By' },
                { columnName: 'Update Date' },
                { columnName: 'Authorization Update By' },
                { columnName: 'Authorization Update Date' },
            ],
            totalItems: 0,
            pageSize: PAGE_SIZE,
            currentPage: 1,
            startingIndex: 1,
            isResetPage: false,
            searchValueBy: {
                date: '' as unknown,
            },
            userRoleData: [] as userRole[]
        }
    },
    async mounted() {
        await this.requestUserRoleList();
        await this.getCustomerInformationReportData();
    },
    computed: {
        formattedDate(): string {
            return this.searchValueBy.date ? this.$datetime.formatVueDatePicker(this.searchValueBy.date as Date) : '';
        },
    },
    methods: {
        async getCustomerInformationReportData() {
            const requestBody: RPT203000111_REQ_BODY = {
                pageNumber: this.currentPage,
                pageSize: PAGE_SIZE,
                applicationDate: this.searchValueBy.date ? this.$datetime.convertDateToString(this.searchValueBy.date as Date) : '',
            }
            const responseData = await this.$request.getRequest('RPT20300111', requestBody, false) as RPT203000111_RES_BODY;
            if (responseData.header.result) {
                this.customerInformationReportData = this.onMappingTableData(responseData.body.list);
                this.totalItems = responseData.body.totalCount;
            }
        },

        async exportToExcel() {
            const customHeader = [
                'Branch Code',
                'Biz CID',
                'Biz Name',
                'Username',
                'User Role',
                'Account Assignation',
                'Phone Number',
                'Email ID',
                'Date Of Birth',
                'Address',
                'User Type',
                'Authorization Status',
                'Create By',
                'Create Date',
                'Authorization Create By',
                'Authorization Create Date',
                'Update By',
                'Update Date',
                'Authorization Update By',
                'Authorization Update Date'
            ]

            const reqBody = {
                applicationDate: this.searchValueBy.date ? this.$datetime.convertDateToString(this.searchValueBy.date as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20300111", reqBody, false) as RPT203000111_RES_BODY;

            const tableForExport = this.onMappingTableData(res.body.list)

            const dataForExport: ExportSheet = {
                data: tableForExport,
                columnHeader: [customHeader],
                sheetName: 'Customer Information Report'
            }
            excel.exportSheet([dataForExport], 'Customer Information Report')
        },

        onMappingTableData(lists) {
            const { ServiceStatusCode, AuthorizationStatusCode } = this.$store.state.CommonCodeModule.commonCode;
            return lists.map((item) => ({
                branchCode: item.applicationBranchCode,
                bizCID: item.customerNo,
                bizName: item.customerName,
                userName: item.userID,
                userRole: this.handleMappingUserRole(this.userRoleData, item.mainUserRoleID),
                accountAssignation: item.accountAssignation,
                phoneNumber: this.$format.formatPhoneNumber(item.cellPhoneNo) ,
                emailID: item.email,
                dateOfBirth: item.birthDate,
                address: item.customerAddress,
                userType: ServiceStatusCode && item.serviceStatusCode != '' ? `${ServiceStatusCode.find((type) => type.key === item.serviceStatusCode)?.key}: ${ServiceStatusCode.find((type) => type.key === item.serviceStatusCode)?.value}` : '',
                authorizationStatus: AuthorizationStatusCode && item.authorizationStatusCode != '' ? `${AuthorizationStatusCode.find((type) => type.key === item.authorizationStatusCode)?.key}: ${AuthorizationStatusCode.find((type) => type.key === item.authorizationStatusCode)?.value}` : '',
                createBy: `${item.firstRegisterTellerID ? `(${item.firstRegisterTellerID})` : ''} ${item.firstRegisterTellerName ? item.firstRegisterTellerName : ''}`,
                createDate: `${item.firstRegisterDate ? this.$datetime.formatStringToStringDate(item.firstRegisterDate) : ''} ${item.firstRegisterTime ? this.$datetime.formatStringToTime(item.firstRegisterTime) : ''}`,
                authorizationCreateBy: `${item.firstRegisterAuthorizerEmployeeNo ? `(${item.firstRegisterAuthorizerEmployeeNo})` : ''} ${item.firstRegisterAuthorizationEmpName ? item.firstRegisterAuthorizationEmpName : ''}`,
                authorizationCreateDate: `${item.firstRegisterAuthorizationDate ? this.$datetime.formatStringToStringDate(item.firstRegisterAuthorizationDate) : ''} ${item.firstRegisterAuthorizationTime ? this.$datetime.formatStringToTime(item.firstRegisterAuthorizationTime) : ''}`,
                updateBy: `${item.lastChangeTellerID ? `(${item.lastChangeTellerID})` : ''} ${item.lastChangeTellerName ? item.lastChangeTellerName : ''}`,
                updateDate: `${item.lastChangeDate ? this.$datetime.formatStringToStringDate(item.lastChangeDate) : ''} ${item.lastChangeTime ? this.$datetime.formatStringToTime(item.lastChangeTime) : ''}`,
                authorizationUpdateBy: `${item.lastChangeAuthorizerEmployeeNo ? `(${item.lastChangeAuthorizerEmployeeNo})` : ''} ${item.lastChangeAuthorizationEmpName ? item.lastChangeAuthorizationEmpName : ''}`,
                authorizationUpdateDate: `${item.lastChangeAuthorizationDate ? this.$datetime.formatStringToStringDate(item.lastChangeAuthorizationDate) : ''} ${item.lastChangeAuthorizationTime ? this.$datetime.formatStringToTime(item.lastChangeAuthorizationTime) : ''}`,
            }));
        },

        handleMappingUserRole(userRoles: userRole[], mainRole: string) : string {
            const matchingUserRole = userRoles.find((user) => user.userRoleID === mainRole);

            if (matchingUserRole) {
                return `${matchingUserRole.userRoleID}: ${matchingUserRole.userRoleName} `;
            }

            return ''
        },

        async requestUserRoleList() {
            const res = await this.$request.getRequest('COM21200113', {}, false) as COM21200113_RES
            if (res.header.result) {
                this.userRoleData = res.body.list;
            }
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getCustomerInformationReportData();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1;
                this.isResetPage = true;
                this.getCustomerInformationReportData();
            } else {
                this.isResetPage = true;
                this.getCustomerInformationReportData();
            }
        },

        handleReset() {
            this.searchValueBy.date = '';
            this.currentPage = 1;
            this.isResetPage = true;
            this.getCustomerInformationReportData();
        }

    }
});


